@import url(https://fonts.googleapis.com/css2?family=Playfair+Display&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Alegreya&family=Open+Sans&family=Oswald&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

body {
   background-color: #25274D;
}

#root {
  -webkit-flex-grow: 1;
          flex-grow: 1;
}

#app {
  background-color: #25274D;
  padding-top: 20px;
  padding-bottom: 20px;
  margin-bottom: 20px;
}

#title {
  text-align: center;
  color: #FFFFFF;
  font-family: 'Playfair Display', serif;
  font-size: 50px;
  padding-bottom: 20px;
  margin-bottom: 20px;
}

#intro-section {
  background-color: #2E9CCA;
}

#title-feature {
  height: 400px;
  width: 100%;
  left: 0; 
  background-color: #AAABB8;
  border: 0;
  padding: 0;
  margin: 0;
}

#project-section {
  height: -webkit-fit-content;
  height: -moz-fit-content;
  height: fit-content;
  width: auto;
  left: 0;
  background-color: #29648A;
  border: 0;
  padding: 0px 30px 0px 30px;
  margin: 0;
  overflow-x: hidden;
}

#projects-title {
  text-align: center;
  color: #FFFFFF;
  font-family: 'Playfair Display', serif;
  font-size: 30px;
  padding-top: 20px;
  margin-bottom: 30px;
}

#grid-container {
  background-color: inherit;
}

.project-container-title {
  color: white;
  font-family: 'Alegreya', serif;
  text-align: center;
}
